import { BudgetItem } from './../../shared/models/budget-item.model';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-budget-item-card',
  templateUrl: './budget-item-card.component.html',
  styleUrls: ['./budget-item-card.component.scss'],
})
export class BudgetItemCardComponent implements OnInit {
  @Input() itemCard: BudgetItem = new BudgetItem(0, '');
  @Output()
  onDeleteClick: EventEmitter<BudgetItem> = new EventEmitter<BudgetItem>();
  @Output()
  cardClick: EventEmitter<BudgetItem> = new EventEmitter<BudgetItem>();

  constructor() {}

  ngOnInit(): void {}

  onDeleteButtonClick(item: BudgetItem) {
    this.onDeleteClick.emit(item);
  }

  onCardClick(item: BudgetItem) {
    this.cardClick.emit(item);
  }
}
